.main-wrapper-pc {
  height: 100%;
  width: 100%;
  overflow: hidden;
  background-image: url("../../assets/denimBg.webp");
  background-repeat: repeat;
  position: relative;
}
.text-main-mobile {
  display: none;
}
.text-main {
  animation: fade-in-text 1.2s ease-in-out;
  z-index: 2;
  position: absolute;
  height: 100%;
  margin-left: 15%;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-direction: column;

}
.main-bg-img {
  /* z-index: 0; */
  position: absolute;
  animation: fade-in-text 1.2s ease-in-out;

  height: 500px;
  width: 500px;
  /* left: 35%; */
  right: 10%;
  /* top: 15%; */
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
  /* border: 1px solid white; */
  /* border-radius: 250px; */

  background: black url("../../assets/dp.JPG") bottom;
  background-size: cover;
  border-radius: 50%;
  box-shadow: 0 0 50px 5px #000;
  transform: perspective(5px) translateZ(2);
  transition-property: box-shadow, transform, scale, translate;
  transition-duration: 0.3s;
  /* object-fit: cover; */

  /* background-repeat: no-repeat;
  background-blend-mode: saturation; */
}
.main-bg-img:hover {
  box-shadow: 0 0 80px 1px #000;
  scale : 1.01;
  translate: 0 -10px; 

}

@keyframes fade-in-text {
  0% {
    opacity: 0;
     scale : 1.1;
  }
  100% {
    opacity: 1;
    transform: 1;
  }
}
.tap-anywhere-class {
  display: none;
}
@media screen and (max-width: 767px) {
  .text-main {
    display: none;
  }
  .text-main-mobile {
    /* animation: fade-in-text 1.2s ease-in-out; */
    z-index: 2;
    position: absolute;
    height: 100%;
    width: 100%;
    /* margin-left: 15%; */
    display: flex;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
  }
  .main-bg-img {
    right: -30%;
    /* top: 50%; */
    /* right: -30%;
    top: 15%;
    display: none; */
  }
  .tap-anywhere-class {
    display: block;
    position: absolute;
    bottom: 1%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    /* right: 50%; */
    /* left: 50%; */
  }
  .tap-anywhere-class > div {
    animation: Arrowbobbing 1.5s ease-in-out infinite;
  }
}
